@import "../../Styles/variables";

.chatPageBlockOpen {
  width: 300px;
  border-radius: 20px;
  right: 0;
  bottom: 0;
  position: fixed;
  transition: .7s;

  .chatTitle {

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 1rem;
    background-color: $activeTextColor;
    color: $whiteColor;
    border-radius: $borderRadius $borderRadius 0 0;

    .minimizeIcon{
      color: $whiteColor;
    }
  }

}

.chatPageBlockCollapsed{
  @extend .chatPageBlockOpen;
  transform: translateY(490px);
  transition: .7s;
}