.main {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.05);
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
}

.progress {
    animation: 1.1s infinite scaling;
}

.progress span {
    color: rgb(154, 146, 199);
    border-radius: 50%;
    box-shadow: 0 0 10px rgb(154, 146, 199);
}

@keyframes scaling {
    from {
        transform: scale(1);
        color: #434ee7;
    }

    50% {
        transform: scale(1.5);
        color: white;
    }

}